import React from 'react';
import { Card, Tooltip, Button, Switch,Slider } from 'antd';
import Chart from '../views/msv2-chart';
import './view-comp-style.less';
import { connect } from "react-redux";
import {ReloadOutlined ,RetweetOutlined,QuestionOutlined } from '@ant-design/icons';
import {reset, updateEdgeReorder, updateNeedOrder} from '../store/actions';


class View extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      resetcheck:'default',
      msvSwitch:true,
      kelpSwitch:true,
      streamSwitch:false,
      unfoldSwitch:true,
      msvnodeSwitch:true,
      threshold:30,
      orderweight:50,
      maxmapping:80,
      colorSwitch:false,
    };
    this.resetButton = this.resetButton.bind(this) 
    this.unfoldSwitch = this.unfoldSwitch.bind(this) 
    this.nodeReorder = this.nodeReorder.bind(this) 
    this.edgeReorder = this.edgeReorder.bind(this) 
    this.streamSwitch = this.streamSwitch.bind(this) 
    this.MSVSwitch = this.MSVSwitch.bind(this)
  }

  componentDidMount() {
    const { timeSpan ,nodeOrder} = this.props;
    Chart.init(this.container,timeSpan, nodeOrder, this.props.dispatch);
  }

  componentDidUpdate(prevProps, prevState) {
    const { timeSpan, unfoldDay,unfoldDiff,nodeOrder,focusSpan,needOrder} = this.props;
    if(prevProps.msvSwitch !== this.state.msvSwitch  || prevProps.needOrder !== needOrder || prevProps.timeSpan !== timeSpan || prevProps.unfoldSwitch !== this.state.unfoldSwitch || prevProps.unfoldDay !== unfoldDay || prevProps.nodeOrder.toString() !== nodeOrder.toString()){    
      Chart.update(timeSpan,unfoldDay,unfoldDiff,nodeOrder,focusSpan,needOrder,this.state.threshold,this.state.maxmapping,this.state.orderweight,this.state.unfoldSwitch, this.state.msvSwitch,this.state.kelpSwitch ,this.state.colorSwitch,this.props.dispatch);
    }
  }
  resetButton() {
    this.props.dispatch(reset())
  }
  unfoldSwitch(checked){
    this.setState({
      unfoldSwitch:checked,
    })
  }
  colorSwitch(checked){
    this.setState({
      colorSwitch:checked,
    })
  }
  streamSwitch(checked){
    this.setState({
      streamSwitch:checked,
    })
  }
  nodeReorder(){
    this.props.dispatch(updateNeedOrder(true))
  }
  MSVSwitch(checked){
    this.setState({
      msvSwitch:checked,
    })
  }
  KelpSwitch(checked){
    this.setState({
      kelpSwitch:checked,
    })
  }
  msvnodeSwitch(checked){
    this.setState({
      msvnodeSwitch:checked,
    })
  }
  StrongAfterChange(value){
    this.setState({
      threshold:value,
    })
  }
  MaxAfterChange(value){
    this.setState({
      maxmapping:value,
    })
  }
  OrderWeight(value){
    this.setState({
      orderweight:value,
    })
  }
  edgeReorder(checked){
    if(checked){
      this.props.dispatch(updateEdgeReorder(true))
    }else{
      this.props.dispatch(updateEdgeReorder(false))
    }
  }

  render() {
    return (
      <Card className="view view-i" title="Nested Matrix Design" extra={

        <div height="30px">   
                <Tooltip title="Set the height max mapping of the bars in the overview matrix and stacked bar chart.
                The smaller the value, the higher the bar height.
                ">
                <div style={{float:"left",marginTop:"4px"}}> &ensp; Max: &ensp; </div>
                </Tooltip>

                <div style={{width:"100px",float:"left"}}>
                    <Slider tooltipPlacement='bottom' defaultValue={90} min={65} tooltipVisible={false} onChange={(value)=>this.MaxAfterChange(value)} />
                </div>
                <Tooltip title="Set the threshold of the difference mask.
                The smaller the value, the more nodes are selected. ">
                <div style={{float:"left",marginTop:"4px"}}> &ensp; Threshold: &ensp; </div>
                  </Tooltip>

                <div style={{width:"100px",float:"left"}}>
                    <Slider tooltipPlacement='bottom' defaultValue={30} onChange={(value)=>this.StrongAfterChange(value)} />
                </div>
              <Tooltip title="Set the focus of our node reordering strategy, where 0 for the detail matrices only and 100 for the overview matrix only. "><div style={{float:"left",marginTop:"4px"}}>  &ensp; OrderWeight: &ensp;  </div>
              </Tooltip>
                
                <div style={{width:"100px",float:"left"}}>
                    <Slider tooltipPlacement='bottom' defaultValue={50} onAfterChange={(value)=>this.OrderWeight(value)} />
                </div>
                <div style={{float:"left"}}>
                <Tooltip title="Reorder the nodes in the nested matrix design. ">
                    <div style={{marginTop:"4px",float:"left"}}>
                      &ensp; Reorder: &ensp;
                  <Button shape="circle" type={this.state.resetcheck} onClick={this.nodeReorder} icon={<RetweetOutlined  style={{color:"#3c374a"}} />} size={'small'} ></Button>
                    </div>

          </Tooltip>
 
                <Tooltip title="Set the type of the detail matrix you will unfold next: the difference or original graph detail matrix.">
                    <div style={{marginTop:"4px",float:"left"}}>
                    &ensp; UnfoldType: &ensp;
                  <Switch checkedChildren="Diff" unCheckedChildren="Origin" defaultChecked onChange={(checked)=>this.unfoldSwitch(checked)}/></div>
                </Tooltip>

                <Tooltip title="Fold all unfolded detail matrices">
                    <div style={{marginTop:"4px",float:"left"}}>
                &ensp; FoldMatrix: &ensp; 
                  <Button shape="circle" type={this.state.resetcheck} onClick={this.resetButton} icon={<ReloadOutlined  style={{color:"#3c374a"}} />} size={'small'} ></Button>
                </div>
                </Tooltip>

                {/* <Tooltip title="Display the matrices or not">
                    <div style={{marginTop:"4px",float:"left"}}>
                &ensp; Matrix: &ensp; 
                  <Switch checkedChildren="On" unCheckedChildren="Off" defaultChecked onChange={(checked)=>this.MSVSwitch(checked)}/></div>
                </Tooltip>  */}

                <Tooltip title="Display the difference mask or not">
                    <div style={{marginTop:"4px",float:"left"}}>
                &ensp; DiffMask: &ensp; 
                  <Switch checkedChildren="On" unCheckedChildren="Off" defaultChecked onChange={(checked)=>this.KelpSwitch(checked)}/></div>
                </Tooltip>   


                <Tooltip title="Choose the focus mode of the overview matrix and difference mask">
                    <div style={{marginTop:"4px",float:"left"}}>
                &ensp; Mode: &ensp; 
                  <Switch checkedChildren="Weight" unCheckedChildren="Degree" onChange={(checked)=>this.colorSwitch(checked)}/></div>
                </Tooltip>


                    <div style={{marginTop:"4px",float:"left"}}>
                    &ensp;
                <Tooltip placement='bottomRight' overlayStyle={{minWidth:650}} title={(<span>Welcome to DiffSeer! 
<br/> 1. You can double-click the column of the overview matrix to unfolded the detail matrix. 
<br/>2. You can double-click the unfolded detail matrix to fold it.
 <br/>3. You can click the FoldMatrix button to fold all unfolded matrices.
 <br/>4. You can choose the type of the unfolded detail matrices with UnfoldType switch.
<br/>5. You can set the OrderWeight then click the Reorder button to reorder the nodes. 
 <br/>6. You can display or hide the difference mask with the DiffMask switch.
 <br/>7. You can set the threshold of the difference mask with the Threshold slider.
 <br/>8. You can change the mode of the nested matrix design with the Mode switch.
 <br/>9. You can change the height of bars in the overview matrix and stacked bar chart with the Max slider.
<br/>More detailed explanations can be obtained by hovering over the interactive components.
</span>)}>
                &ensp; Tutorial: &ensp; 
                    <Button shape="circle" type={this.state.resetcheck} onClick={this.resetButton} icon={<QuestionOutlined  style={{color:"#3c374a"}} />} size={'small'} ></Button>
                </Tooltip>
                    </div>

        </div>
      </div>
      }>
        {/* <div className="view-container" ref={ ref => this.container = ref }></div> */}
<div className="view-container" ref={ref => this.container = ref} style={{ position:"relative" }}>
          <div style={{ width: '4%', display: 'flex', flexDirection: 'Column', justifyContent: 'flex-start', alignItems: 'center', position: "absolute", left: "7px", top: "8px" }}>
            <svg id="scale-diff" />
            <svg id="scale-org" style={{ margin: "5 0" }}/>
          </div>
        </div>
        <div className="tooltip"></div>
      </Card>
    );
  }
}
const mapStateToProps = (state, props) => ({
  timeSpan:state.timeSpan,
  unfoldDay:state.unfoldDay,
  unfoldDiff:state.unfoldDiff,
  nodeOrder:state.nodeOrder,
  focusSpan:state.focusSpan,
  needOrder:state.needOrder,
  unfoldSwitch:state.unfoldSwitch,
  MSVorder:state.MSVorder,
  edgeReorder:state.edgeReorder,
  streamSwitch:state.streamSwitch,
});

const mapDispatchToProps = dispatch => ({
  dispatch
});

export default connect(mapStateToProps, mapDispatchToProps)(View);