import React from 'react';
import { Card, Tooltip, Button } from 'antd';
import Chart from '../views/project-timeline';
import './view-comp-style.less';
import { connect } from "react-redux";
import {QuestionOutlined } from '@ant-design/icons';
import {updateSelectedDate} from '../store/actions';

class View extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      resetcheck:'default'
    };
    this.resetButton = this.resetButton.bind(this) 
  }

  componentDidMount() {
    Chart.init(this.container, this.props.dispatch);
  }

  componentDidUpdate(prevProps, prevState) {
    const { selectedDate } = this.props;
    if(prevProps.selectedDate !== selectedDate){
      Chart.update(selectedDate, this.props.dispatch);
    }
  }
  resetButton() {
    this.props.dispatch(updateSelectedDate([]))
  }

  render() {
    return (
      <Card className="view view-a" title="TimeLine" extra={
        <div>                
                <Tooltip title={(<span>Welcome to DiffSeer!<br/> You can brush the timeline to choose a period of interest, and we set a minimum time span for it. </span>)}>
 <Button shape="circle" type={this.state.resetcheck} icon={<QuestionOutlined  style={{color:"#3c374a"}} />} size={'small'} ></Button>
                </Tooltip>
        </div>
      }>
        <div className="view-container" ref={ ref => this.container = ref }></div>
        <div className="tooltip"></div>
      </Card>
    );
  }
}
const mapStateToProps = (state, props) => ({
  selectedDate:state.selectedDate
});

const mapDispatchToProps = dispatch => ({
  dispatch
});

export default connect(mapStateToProps, mapDispatchToProps)(View);